.popup-login {
  display: block;
  min-width: 442px;
  position: absolute;
  top: 100px;
  left: 36%;
  transform: translate(4px, 0);
  z-index: 5;
  background-color: $beige;

  @include tablet {
    left: 21%;
    top: 81px;
  }

  @include mobile {
    min-width: 270px;
    top: 50px;
    left: 8%;
  }
}

.popup-login--hide {
  display: none;
}

.popup-login picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.popup-login img {
  width: 100%;
  object-fit: cover;

  @include mobile {
    height: 84px;
  }
}

.popup-login__wrapper {
  position: relative;
  padding: 189px 41px 46px;

  @include tablet {
    padding: 188px 50px 49px;
  }

  @include mobile {
    padding: 121px 15px 33px;
  }
}

.popup-login__close-button {
  position: absolute;
  cursor: pointer;
  top: 20px;
  right: 20px;
  border: none;
  background: transparent;
  display: block;
  padding: 0;
  transition: all 0.3s ease-in;

  @include tablet {
    width: 20px;
    height: 20px;
  }

  @include mobile {
    top: 14px;
    right: 20px;
  }
}

.popup-login__close-button svg {
  fill: $dark;
  transition: all 0.3s ease-in;
}

.popup-login__close-button:hover svg {
  fill: $white;
  transition: all 0.3s ease-in;
}

.popup-login__close-button:active svg {
  opacity: 0.3;
}

.popup-login__form {
  display: flex;
  flex-direction: column;
}

.popup-login__form h2 {
  display: block;
  margin: 0 0 28px 0;
  padding: 0;
  font-size: 34px;
  line-height: 44px;
  color: $dark;
  text-align: center;

  @include cormorant-bold;

  @include mobile {
    font-size: 24px;
    line-height: 31px;
    margin: 0 0 22px 0;
  }
}

.popup-login__form ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.popup-login__form li {
  margin: 0 0 32px 0;
  display: flex;
  flex-direction: column;

  @include mobile {
    margin: 0 0 28px;
  }
}

.popup-login__form li:last-child a {
  display: block;
  padding: 5px 0;
  font-size: 12px;
  line-height: 18px;
  color: $pale-text;
  align-self: flex-end;
  transition: all 0.3s ease-in;

  @include poppins-medium;
}

.popup-login__form li:last-child a:hover {
  color: $dark;
  transition: all 0.3s ease-in;
}

.popup-login__form input {
  border: none;
  background: transparent;
  padding: 9px 3px;
  border-bottom: 1px solid $dark;
  outline: 0;
  outline-offset: none;
  transition: all 0.3s ease-in;
}

.popup-login__form input:hover {
  border-bottom: 1px solid $white;
  transition: all 0.3s ease-in;
}

.popup-login__form input:focus {
  border-bottom: 1px solid $white;
  transition: all 0.3s ease-in;
}

.popup-login__form input::placeholder {
  font-size: 14px;
  line-height: 21px;
  color: $pale-text;

  @include poppins-medium;
}

.popup-login__sign-up {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0 0;

  @include mobile {
    padding: 16px 0 0;
  }
}

.popup-login__sign-up p {
  display: block;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  color: $pale-text;

  @include poppins-medium;
}

.popup-login__sign-up a {
  display: block;
  margin: 0 0 0 10px;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  color: $dark;
  transition: all 0.3s ease-in;

  @include poppins-medium;
}

.popup-login__sign-up a:hover {
  box-shadow: 0 0 2px 2px $pale-text;
  transition: all 0.3s ease-in;
}

.popup-login__sign-up a:active {
  box-shadow: 0 0 3px 3px $dark;
}

.popup-login__button {
  transform: translate(1px, 7px);
}
