  font-weight: 700;
  font-style: normal;
}

.landing-header__navigation ol {
  margin: 0;
  padding: 0;
}

.landing-header__navigation li {
  cursor: pointer;
}

.landing-header__navigation li::marker {
  font-size: 24px;
  line-height: 36px;
  color: #1d1613;
  transition: all 0.3s ease-in;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
}

.landing-header__navigation a {
  font-size: 24px;
  line-height: 36px;
  color: #1d1613;
  transition: all 0.3s ease-in;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
}

.landing-header__navigation li:hover a {
  color: #ffffff;
  transition: all 0.3s ease-in;
}

.landing-header__navigation li:hover::marker {
  color: #ffffff;
  transition: all 0.3s ease-in;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}
.overlay--active {
  opacity: 1;
  visibility: visible;
  z-index: 3;
}

.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  position: relative;
}

.page-header {
  width: 100%;
  flex: 0 0 auto;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
  z-index: 2;
  overflow: hidden;
  background-color: #ffffff;
}
