  .page-header {
    height: 100px;
    transition: height 1s ease 0s;
  }
}
@media (max-width: 767px) {
  .page-header {
    height: 93px;
  }
}

.page-header__wrapper {
  padding: 20px 20px 18px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1210px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .page-header__wrapper {
    position: relative;
    padding: 28px 6% 80px;
  }
}
@media (max-width: 767px) {
  .page-header__wrapper {
    padding: 17px 5% 80px;
  }
}

.page-header__wrapper a {
  transition: all 0.3s ease-in;
}

.page-header__wrapper a:hover {
  color: #a87b62;
  transition: all 0.3s ease-in;
}
