.landing-header {
  width: 100%;
  height: 100vh;
  background-color: $pink;
}

.landing-header__wrapper {
  padding: 0 15%;
  margin: 0 auto;
  align-items: center;
}

.landing-header__wrapper h1 {
  font-size: 32px;
  line-height: 44px;
  color: $dark;

  @include poppins-bold;
}

.landing-header__navigation ol {
  margin: 0;
  padding: 0;
}

.landing-header__navigation li {
  cursor: pointer;
}

.landing-header__navigation li::marker {
  font-size: 24px;
  line-height: 36px;
  color: $dark;
  transition: all 0.3s ease-in;

  @include poppins-medium;
}

.landing-header__navigation a {
  font-size: 24px;
  line-height: 36px;
  color: $dark;
  transition: all 0.3s ease-in;

  @include poppins-medium;
}

.landing-header__navigation li:hover {
  a {
    color: $white;
    transition: all 0.3s ease-in;
  }
}

.landing-header__navigation li:hover::marker {
  color: $white;
  transition: all 0.3s ease-in;
}
